import { Controller, Decoration } from '@ali/4ever-cangjie';
import { PASTE_DECORATION } from '../../utils/constants';

export default function hidePasteButton(controller: Controller) {
  const { data, decorations } = controller.value;
  const { _clipboardPanel } = data;
  const newDecorations: Decoration[] = decorations.filter(
    (item) => item.mark.type !== PASTE_DECORATION,
  );
  if (_clipboardPanel) {
    controller
      .setData({
        ...data,
        _clipboardPanel: '',
      })
      .setDecorations(newDecorations);
  }
}
